import Team from "../../domain/Team";
import TeamId from "../../domain/TeamId";
import TeamName from "../../domain/TeamName";
import ChoozrId from "../../domain/ChoozrId";
import TeamMembersCount from "../../domain/TeamMembersCount";
import TeamDetails from "../../domain/TeamDetails";
import MemberName from "../../domain/MemberName";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { getFirestore, collection, query, limit, getDocs, addDoc, where, getDoc, doc } from "firebase/firestore";
import { app } from "./FirebaseInitializer";
export default class FirebaseTeamOutputAdapter {
    constructor() {
        this.snapshotToTeamMembersCount = (record) => {
            return new TeamMembersCount(new TeamId(record.id), record.get("membersCount"));
        };
        this.snapshotToTeam = (record) => {
            return new Team(new TeamId(record.id), new TeamName(record.get("name")), new ChoozrId(record.get("choozrId")));
        };
    }
    async getChoozrTeamsWith(choozrId, loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const first = query(collection(db, "Team"), limit(50), where("choozrId", "==", choozrId.value));
        const documentSnapshots = await getDocs(first);
        return documentSnapshots.docs.map(this.snapshotToTeam);
    }
    async getTeamDetailsWith(teamId, loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const teamSnapshot = await getDoc(doc(db, "Team", teamId.value));
        const teamName = new TeamName(teamSnapshot.get("name"));
        const firstMembers = query(collection(db, "Member"), limit(50), where("teamId", "==", teamId.value));
        const membersSnapshots = await getDocs(firstMembers);
        const membersNames = membersSnapshots.docs.map((member) => new MemberName(member.get("name")));
        return new TeamDetails(teamId, teamName, membersNames);
    }
    async getTeamsMembersCountFrom(choozrId, loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const first = query(collection(db, "Team"), limit(50), where("choozrId", "==", choozrId.value));
        const documentSnapshots = await getDocs(first);
        return documentSnapshots.docs.map(this.snapshotToTeamMembersCount);
    }
    async createTeamWith(choozrId, teamName, loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const docRef = await addDoc(collection(db, "Team"), {
            name: teamName.value,
            choozrId: choozrId.value,
            membersCount: 0
        });
        return new Team(new TeamId(docRef.id), teamName, choozrId);
    }
}
