import Choozr from "../../domain/Choozr";
import ChoozrId from "../../domain/ChoozrId";
import ChoozrName from "../../domain/ChoozrName";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { getFirestore, collection, query, limit, getDocs, addDoc } from "firebase/firestore";
import { app } from "./FirebaseInitializer";
export default class FirebaseChoozrOutputAdapter {
    constructor() {
        this.snapshotToChoozr = (record) => {
            return new Choozr(new ChoozrId(record.id), new ChoozrName(record.get("name")));
        };
    }
    async getChoozrsWith(loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const first = query(collection(db, "Choozr"), limit(50));
        const documentSnapshots = await getDocs(first);
        return documentSnapshots.docs.map(this.snapshotToChoozr);
    }
    async createChoozrWith(choozrName, loginParameters) {
        const auth = getAuth();
        await signInWithEmailAndPassword(auth, loginParameters.apiKey, loginParameters.appId);
        const db = getFirestore(app);
        const docRef = await addDoc(collection(db, "Choozr"), {
            name: choozrName.value,
        });
        return new Choozr(new ChoozrId(docRef.id), choozrName);
    }
}
